export interface Participant {
  /** @format GUID */
  id?: string;
  member?: Member;
  challenge?: EntitySummary;
  timeFrame?: TimeInterval;
  stepsSummary?: StepsSummary;
  orderIds?: string[];
  transitions?: StateTransition[];
  timezone?: string;
  dateFrame?: DateInterval;
  socialGroupId?: string | null;
  performance?: number;
  joinPath?: JoinPath;
  /** Certificate issued to participant on successful program complete */
  certificate?: Certificate;
}

export interface StepEvent {
  stepId?: string;
  transition?: ParticipantStepStateTransition;
}

export interface ParticipantStepStateTransition {
  state?: ParticipantStepStateWithLiterals;
  occurredAt?: Date | null;
}

export enum ParticipantStepState {
  PENDING = 'PENDING',
  RUNNING = 'RUNNING',
  COMPLETED = 'COMPLETED',
  SKIPPED = 'SKIPPED',
  CONFIRMED = 'CONFIRMED',
  REJECTED = 'REJECTED',
  OVERDUE = 'OVERDUE',
  FAILED = 'FAILED',
}

/** @enumType */
export type ParticipantStepStateWithLiterals =
  | ParticipantStepState
  | 'PENDING'
  | 'RUNNING'
  | 'COMPLETED'
  | 'SKIPPED'
  | 'CONFIRMED'
  | 'REJECTED'
  | 'OVERDUE'
  | 'FAILED';

export enum State {
  JOIN_REQUESTED = 'JOIN_REQUESTED',
  INVITED = 'INVITED',
  JOIN_REJECTED = 'JOIN_REJECTED',
  PAYMENT_REQUESTED = 'PAYMENT_REQUESTED',
  PAYMENT_STARTED = 'PAYMENT_STARTED',
  JOINED = 'JOINED',
  LEFT = 'LEFT',
  REMOVED = 'REMOVED',
  RUNNING = 'RUNNING',
  COMPLETED = 'COMPLETED',
  FAILED = 'FAILED',
  PAYMENT_STARTED_OFFLINE = 'PAYMENT_STARTED_OFFLINE',
  SUSPENDED = 'SUSPENDED',
  AUTO_REMOVED = 'AUTO_REMOVED',
}

/** @enumType */
export type StateWithLiterals =
  | State
  | 'JOIN_REQUESTED'
  | 'INVITED'
  | 'JOIN_REJECTED'
  | 'PAYMENT_REQUESTED'
  | 'PAYMENT_STARTED'
  | 'JOINED'
  | 'LEFT'
  | 'REMOVED'
  | 'RUNNING'
  | 'COMPLETED'
  | 'FAILED'
  | 'PAYMENT_STARTED_OFFLINE'
  | 'SUSPENDED'
  | 'AUTO_REMOVED';

