export interface Container {}

export interface Step {
  stepId?: string;
}

export interface ChallengeStepSettings {
  general?: GeneralSettings;
  embedding?: EmbeddingSettings;
}

export interface Feedback {
  items?: FeedbackItem[];
  createdAt?: Date | null;
  updatedAt?: Date | null;
  quiz?: FeedbackItem[];
}

export interface QuizSubmission {
  quizSubmissionId?: string;
  score?: number | null;
  grade?: number | null;
  rightAnswers?: number | null;
  passingGrade?: number | null;
}

export interface GetParticipantStepRequest {
  /** @format GUID */
  challengeId: string;
  /** @format GUID */
  participantId: string;
  stepId: string;
  timeInterval?: TimeInterval;
  descriptionFieldSet?: DescriptionFieldSetWithLiterals;
}

export interface GetParticipantStepResponse {
  step?: ParticipantStep;
  subSteps?: ParticipantStep[];
}

export interface ResolveParticipantStepRequest {
  /** @format GUID */
  challengeId: string;
  /** @format GUID */
  participantId: string;
  stepId: string;
  status?: ResolutionStatusWithLiterals;
  feedback?: Feedback;
  actionId?: string;
  /** @format GUID */
  quizSubmissionId?: string | null;
}

export enum ResolutionStatus {
  COMPLETED = 'COMPLETED',
  SKIPPED = 'SKIPPED',
  UNDO = 'UNDO',
  QUIZ_SUBMIT = 'QUIZ_SUBMIT',
}

/** @enumType */
export type ResolutionStatusWithLiterals =
  | ResolutionStatus
  | 'COMPLETED'
  | 'SKIPPED'
  | 'UNDO'
  | 'QUIZ_SUBMIT';

export interface ResolveParticipantStepResponse {
  step?: ParticipantStep;
  /** @format GUID */
  actionId?: string;
}

export interface StepResolved {
  challengeId?: string;
  participantId?: string;
  memberId?: string;
  stepTitle?: string;
  participantStepId?: string;
  challengeStepId?: string;
  /** TODO why it is repeated? */
  feedback?: Feedback[];
  /** Title of the program. Used in Notifications */
  programName?: string;
  /**
   * user id for program owner
   * @format GUID
   */
  ownerId?: string;
  /**
   * owner's contact id
   * @format GUID
   */
  ownerContactId?: string;
  /**
   * participants contact
   * @format GUID
   */
  participantContactId?: string;
  /** info about quiz submission in that step */
  quizSubmissionData?: QuizSubmissionData;
  /** info about if participant completed step successfully or not */
  status?: StatusWithLiterals;
}

export interface QuizSubmissionData {
  /** @format GUID */
  submissionId?: string | null;
  earnedScore?: number | null;
  passingGrade?: number | null;
}

export enum Status {
  /** step completed */
  COMPLETED = 'COMPLETED',
  /** participant failed to resolve step */
  FAILED = 'FAILED',
}

/** @enumType */
export type StatusWithLiterals = Status | 'COMPLETED' | 'FAILED';

export interface UpdateStepFeedbackRequest {
  /** @format GUID */
  challengeId: string;
  /** @format GUID */
  participantId: string;
  stepId: string;
  feedback?: Feedback;
  /** @format GUID */
  actionId?: string;
}

export interface UpdateStepFeedbackResponse {
  step?: ParticipantStep;
  /** @format GUID */
  actionId?: string;
}

export interface GetMediaUploadInfoRequest {
  /** @format GUID */
  challengeId: string;
  /** @format GUID */
  participantId: string;
  fileName?: string;
}

export interface GetMediaUploadInfoResponse {
  uploadUrl?: string;
  uploadToken?: string;
}

export interface ListParticipantSectionsRequest {
  /** @format GUID */
  challengeId: string;
  /** @format GUID */
  participantId: string;
  descriptionFieldSet?: DescriptionFieldSetWithLiterals;
}

export interface ListParticipantSectionsResponse {
  sections?: ParticipantSection[];
}

export interface ParticipantSection {
  id?: string;
  source?: ChallengeSection;
  steps?: ParticipantStep[];
  transitions?: ParticipantSectionStateTransition[];
  progress?: Progress;
}

export interface WaitingDate {
  startDate?: string;
}

export interface WaitingDependency {
  sectionId?: string;
}

export interface Running {
  dateInterval?: DateInterval;
}

export interface Completed {}

export interface Overdue {}

export interface ChallengeSection {
  /** challenge section id. */
  id?: string;
  /** challenge section settings. */
  settings?: Settings;
  createdAt?: Date | null;
  updatedAt?: Date | null;
  /** list of challenge steps laying inside the section. */
  steps?: ChallengeStep[];
}

export interface Settings {
  /** section description(title, details, image...) */
  description?: ObjectDescription;
  /** condition for the section start. */
  startCondition?: StartCondition;
}

export interface ParticipantSectionStateTransition
  extends ParticipantSectionStateTransitionStateOneOf {
  waitingDate?: WaitingDate;
  waitingDependency?: WaitingDependency;
  running?: Running;
  completed?: Completed;
  overdue?: Overdue;
  occurredAt?: Date | null;
}

/** @oneof */
export interface ParticipantSectionStateTransitionStateOneOf {
  waitingDate?: WaitingDate;
  waitingDependency?: WaitingDependency;
  running?: Running;
  completed?: Completed;
  overdue?: Overdue;
}

export interface Progress {
  nTotalSteps?: string;
  nCompletedSteps?: string;
}

export interface GetParticipantSectionRequest {
  /** @format GUID */
  challengeId: string;
  /** @format GUID */
  participantId: string;
  /** @format GUID */
  sectionId: string;
  descriptionFieldSet?: DescriptionFieldSetWithLiterals;
}

export interface GetParticipantSectionResponse {
  section?: ParticipantSection;
}

export interface MyProgramRequest {
  programId: string;
}

export interface MyProgramResponse {
  participant?: Participant;
}

export interface MyProgramStepRequest {
  programId: string;
  programStepId: string;
  descriptionFieldSet?: DescriptionFieldSetWithLiterals;
}

export interface MyProgramStepResponse {
  participantStep?: ParticipantStep;
}

export interface MyProgramSectionRequest {
  programId: string;
  programSectionId: string;
  descriptionFieldSet?: DescriptionFieldSetWithLiterals;
}

export interface MyProgramSectionResponse {
  participantSection?: ParticipantSection;
}

export interface DomainEvent extends DomainEventBodyOneOf {
  createdEvent?: EntityCreatedEvent;
  updatedEvent?: EntityUpdatedEvent;
  deletedEvent?: EntityDeletedEvent;
  actionEvent?: ActionEvent;
  /** Event ID. With this ID you can easily spot duplicated events and ignore them. */
  id?: string;
  /**
   * Fully Qualified Domain Name of an entity. This is a unique identifier assigned to the API main business entities.
   * For example, `wix.stores.catalog.product`, `wix.bookings.session`, `wix.payments.transaction`.
   */
  entityFqdn?: string;
  /**
   * Event action name, placed at the top level to make it easier for users to dispatch messages.
   * For example: `created`/`updated`/`deleted`/`started`/`completed`/`email_opened`.
   */
  slug?: string;
  /** ID of the entity associated with the event. */
  entityId?: string;
  /** Event timestamp in [ISO-8601](https://en.wikipedia.org/wiki/ISO_8601) format and UTC time. For example, `2020-04-26T13:57:50.699Z`. */
  eventTime?: Date | null;
  /**
   * Whether the event was triggered as a result of a privacy regulation application
   * (for example, GDPR).
   */
  triggeredByAnonymizeRequest?: boolean | null;
  /** If present, indicates the action that triggered the event. */
  originatedFrom?: string | null;
  /**
   * A sequence number that indicates the order of updates to an entity. For example, if an entity was updated at 16:00 and then again at 16:01, the second update will always have a higher sequence number.
   * You can use this number to make sure you're handling updates in the right order. Just save the latest sequence number on your end and compare it to the one in each new message. If the new message has an older (lower) number, you can safely ignore it.
   */
  entityEventSequence?: string | null;
}

/** @oneof */
export interface DomainEventBodyOneOf {
  createdEvent?: EntityCreatedEvent;
  updatedEvent?: EntityUpdatedEvent;
  deletedEvent?: EntityDeletedEvent;
  actionEvent?: ActionEvent;
}

export interface EntityCreatedEvent {
  entityAsJson?: string;
  /** Indicates the event was triggered by a restore-from-trashbin operation for a previously deleted entity */
  restoreInfo?: RestoreInfo;
}

export interface RestoreInfo {
  deletedDate?: Date | null;
}

export interface EntityUpdatedEvent {
  /**
   * Since platformized APIs only expose PATCH and not PUT we can't assume that the fields sent from the client are the actual diff.
   * This means that to generate a list of changed fields (as opposed to sent fields) one needs to traverse both objects.
   * We don't want to impose this on all developers and so we leave this traversal to the notification recipients which need it.
   */
  currentEntityAsJson?: string;
}

export interface EntityDeletedEvent {
  /** Entity that was deleted. */
  deletedEntityAsJson?: string | null;
}

export interface ActionEvent {
  bodyAsJson?: string;
}

export interface MessageEnvelope {
  /**
   * App instance ID.
   * @format GUID
   */
  instanceId?: string | null;
  /**
   * Event type.
   * @maxLength 150
   */
  eventType?: string;
  /** The identification type and identity data. */
  identity?: IdentificationData;
  /** Stringify payload. */
  data?: string;
}

export interface IdentificationData extends IdentificationDataIdOneOf {
  /**
   * ID of a site visitor that has not logged in to the site.
   * @format GUID
   */
  anonymousVisitorId?: string;
  /**
   * ID of a site visitor that has logged in to the site.
   * @format GUID
   */
  memberId?: string;
  /**
   * ID of a Wix user (site owner, contributor, etc.).
   * @format GUID
   */
  wixUserId?: string;
  /**
   * ID of an app.
   * @format GUID
   */
  appId?: string;
  /** @readonly */
  identityType?: WebhookIdentityTypeWithLiterals;
}

/** @oneof */
export interface IdentificationDataIdOneOf {
  /**
   * ID of a site visitor that has not logged in to the site.
   * @format GUID
   */
  anonymousVisitorId?: string;
  /**
   * ID of a site visitor that has logged in to the site.
   * @format GUID
   */
  memberId?: string;
  /**
   * ID of a Wix user (site owner, contributor, etc.).
   * @format GUID
   */
  wixUserId?: string;
  /**
   * ID of an app.
   * @format GUID
   */
  appId?: string;
}

export enum WebhookIdentityType {
  UNKNOWN = 'UNKNOWN',
  ANONYMOUS_VISITOR = 'ANONYMOUS_VISITOR',
  MEMBER = 'MEMBER',
  WIX_USER = 'WIX_USER',
  APP = 'APP',
}

/** @enumType */
export type WebhookIdentityTypeWithLiterals =
  | WebhookIdentityType
  | 'UNKNOWN'
  | 'ANONYMOUS_VISITOR'
  | 'MEMBER'
  | 'WIX_USER'
  | 'APP';
